<template>
  <v-layout row justify-end>
    <v-btn @click="Logout" v-show="verifyUser" flat slot="activator">
      <span>logout</span>
      <v-icon right>exit_to_app</v-icon>
    </v-btn>
    <v-dialog v-show="!isNinja" v-model="dialog" persistent max-width="400px">
      <v-btn flat slot="activator">
        <span>login</span>
        <v-icon right>exit_to_app</v-icon>
      </v-btn>
      <v-card>
        <v-card-title class="headline grey lighten-2" primary-title>
          <h2 class="error" v-if="error">{{ error }}</h2>
          <v-spacer></v-spacer>
          <v-icon @click="dialog = false">close</v-icon>
          UCMAS STUDENT TRACKER-LOGIN
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12 sm12 md12>
                <v-text-field v-validate="'required|email'" v-model="email" :error-messages="errors.collect('email')" label="E-mail" data-vv-name="email" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm12 md12>
                <v-text-field v-validate="'required|max:100'" v-model="password" :counter="100" :error-messages="errors.collect('name')" label="Password" data-vv-name="password" required></v-text-field>
              </v-flex>
  
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
  
          <v-btn color="primary" dark @click="submit">login</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>


<script>
  import LoginPost from '../http/PostLogin';
  import Vue from 'vue'
  import VeeValidate from 'vee-validate'
  import VueCookie from 'vue-cookie'
  Vue.use(VueCookie)
  
  Vue.use(VeeValidate)
  

  export default {
    $_veeValidate: {
      validator: 'new'
    },
    data() {
      return {
        isNinja: false,
        error: '',
        dialog: false,
        email: '',
        password: '',
        verifyUser: false,
        dictionary: {
          attributes: {
            email: 'E-mail Address'
            // custom attributes
          },
          custom: {
            password: {
              required: () => 'Password can not be empty',
              max: 'The Password field may not be greater than 100 characters'
              // custom messages
            }
          }
        }
      }
    },
  
    mounted() {
      this.$validator.localize('en', this.dictionary)
      const verifyToken = localStorage.getItem('token')
      if (verifyToken) {
        this.isNinja = true
        this.verifyUser = true
      } else {
        this.isNinja = false
        this.$router.push('/ucmase-login')
      }
    },
  
    methods: {
      async submit() {
        this.$validator.validateAll()
        const user = {
          email: this.email,
          password: this.password
        }
        await LoginPost.LoginUser(user)
          .then(response => {
            if (response.data.status == 'success') {
              this.$cookie.set('token', response.data.token);
              localStorage.setItem('token', response.data.token)
              this.verifyUser = true
              this.dialog = false
              this.isNinja = true
              this.$router.go('/')
            } else if (response.data.status == 'error') {
              this.error = response.data.message
            }
          })
      },
      async Logout() {
        localStorage.removeItem('token');
        this.$router.go('/ucmase-login')
      },
    }
  }
</script>