import axios from 'axios';

const url = 'api/login';

class PostState {
    // Posts
    static LoginUser(data){
      return axios.post(url, {
        email : data.email,
        password: data.password,    
      })
      .then(function(res){
          console.log(res)
          return(res)
      }).catch((err) =>{
          console.log(err)
          return(err)
      })
    }
}

export default PostState;