<template>
  <div>
    <v-card>
    <v-snackbar 
      v-model="snackbar"
      :bottom="y = 'bottom'"
      :multi-line="mode = 'multi-line'"
      :right="x = 'right'"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}
      <v-btn
        color="black"
        flat
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </v-card>
    <v-toolbar flat color="white">
      <v-toolbar-title>AREA</v-toolbar-title>
      <v-card-title>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <h2 class="error" v-if="error">{{ error }}</h2>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <v-btn slot="activator" color="primary" dark class="mb-2" @click="getStates">Create - Area</v-btn>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.areaName" label="Area Name"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                    <v-select 
                    :items="stateItem" 
                    label="State Name"
                    v-model="editedItem.stateName"
                    ></v-select>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.contactPerson" label="Contect Person"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.Email" label="Email"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.Mobile" type="tel" label="Mobile"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
            <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <tr>
          <td>{{ props.item.areaName }}</td>
          <td>{{ props.item.stateName }}</td>
          <td>{{ props.item.contactPerson }}</td>
          <td>{{ props.item.Email }}</td>
          <td>{{ props.item.Mobile }}</td>
          <td class="justify-center layout px-0">
            <v-icon
              small
              class="mr-2"
              @click="editItem(props.item)"
            >
              edit
            </v-icon>
            <v-icon
              small
              @click="deleteItem(props.item)"
            >
              delete
            </v-icon>
          </td>
        </tr>
      </template>
      <v-alert slot="no-results" :value="true" color="error" icon="warning">
        Your search for "{{ search }}" found no results.
      </v-alert>
    </v-data-table>
  </div>
</template>

<script>

  import PostArea  from '../http/PostArea';
  import PostState  from '../http/PostState';
  import Vue from 'vue'
  import VueCookie from 'vue-cookie'
  Vue.use(VueCookie )

  export default {
    name: 'Home',
    data() {
      return {
        snackbar: false,
        color: '',
        y: 'right',
        x: null,
        mode: 'vertical',
        timeout: 4000,
        text: '',
        search: '',
        desserts: [],
        error: '',
        dialog: false,
        editedIndex: -1,
        editedItem: {
          areaName: '',
          stateName: '',
          contactPerson: '',
          Email: '',
          Mobile: ''
        },
        defaultItem: {
          areaName: 'State example Delhi',
          stateName: 'Short name example DL',
          contactPerson: 'Example Siddhartha',
          Email: 'Example@example.com',
          Mobile: '01100001111'
        },
        headers: [
          {text: 'Area Name',align: 'left',sortable: false,value: 'areaName'},
          { text: 'State Name', value: 'stateName' },
          { text: 'Contact Person', value: 'contactPerson' },
          { text: 'Email', value: 'Email' },
          { text: 'Mobile', value: 'Mobile' },
          { text: 'Actions', value: 'Actions' }
        ],
        stateItem: [],
      }
    },
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Create - Area' : 'Edit - Area'
      }
    },

    watch: {
      dialog (val) {
        val || this.close()
      }
    },
    async created(){
      try{
        this.desserts = await PostArea.getPosts();
      }
      catch(err){
        this.error = err.message;
      }
    },    
    methods: {
        async getStates() {
            var data = await PostState.getPosts();
            var list = []
            data.map(function(value) {
                list.push(value.stateName);
            });
            this.stateItem = list
        },
        async deleteItem (item) {
            const index = this.desserts.indexOf(item)
            var r = confirm('Are you sure you want to delete this item?')
            if(r == true){
            this.desserts.splice(index, 1)
            await PostArea.deletePost(item._id);
            this.desserts = await PostArea.getPosts();
            }else{
            return false
            }
        },
        async editItem (item) {
            this.editedIndex = this.desserts.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },
        close() {
            this.dialog = false
            setTimeout(() => {
            this.editedItem = Object.assign({}, this.defaultItem)
            this.editedIndex = -1
            }, 300)
        },
      async save() {
         if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else if (this.editedItem == '') {
          alert('done')
        }else {
            await PostArea.insertPost(this.editedItem)
            .then(data=>{
                if (data == 'Error: Request failed with status code 400') {
                    this.color = 'error'
                    this.snackbar = true;
                    this.text = 'Error in saving data hint: All fields are required and state name should be unique'
                }else if (data.data.status == 'success') {
                    this.color = 'success'
                    this.snackbar = true;
                    this.text = data.data.message
                    this.close()
                }else{
                    this.color = 'error'
                    this.snackbar = true;
                    this.text = 'This Area already exist'
                    }
            }) 
        }
        this.desserts = await PostArea.getPosts();
      },
    },
  }
</script>