<template>
  <div>
    <v-card>
    <v-snackbar 
      v-model="snackbar"
      :bottom="y = 'bottom'"
      :multi-line="mode = 'multi-line'"
      :right="x = 'right'"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}
      <v-btn
        color="black"
        flat
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </v-card>
    <v-toolbar flat color="white">
      <v-toolbar-title>STATE</v-toolbar-title>
      <v-card-title>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <h2 class="error" v-if="error">{{ error }}</h2>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <v-btn slot="activator" color="primary" dark class="mb-2">Create - State</v-btn>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.stateName" label="stateName"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.Abbr" label="Abbr"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.contactPerson" label="Contect Person"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.Email" label="Email"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.Mobile" label="Mobile"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
            <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <tr>
          <td>{{ props.item.stateName }}</td>
          <td>{{ props.item.Abbr }}</td>
          <td>{{ props.item.contactPerson }}</td>
          <td>{{ props.item.Email }}</td>
          <td>{{ props.item.Mobile }}</td>
          <td class="justify-center layout px-0">
            <v-icon
              small
              class="mr-2"
              @click="editItem(props.item)"
            >
              edit
            </v-icon>
            <v-icon
              small
              @click="deleteItem(props.item)"
            >
              delete
            </v-icon>
          </td>
        </tr>
      </template>
      <v-alert slot="no-results" :value="true" color="error" icon="warning">
        Your search for "{{ search }}" found no results.
      </v-alert>
    </v-data-table>
  </div>
</template>

<script>

  import PostState  from '../http/PostState';
  import Vue from 'vue'
  import VueCookie from 'vue-cookie'
  Vue.use(VueCookie )

  export default {
    name: 'Home',
    data() {
      return {
        snackbar: false,
        color: '',
        y: 'right',
        x: null,
        mode: 'vertical',
        timeout: 4000,
        text: '',
        search: '',
        desserts: [],
        error: '',
        dialog: false,
        editedIndex: -1,
        editedItem: {
          stateName: '',
          Abbr: '',
          contactPerson: '',
          Email: '',
          Mobile: ''
        },
        defaultItem: {
          stateName: 'State example Delhi',
          Abbr: 'Short name example DL',
          contactPerson: 'Example Siddhartha',
          Email: 'Example@example.com',
          Mobile: '01100001111'
        },
        headers: [
          {
            text: 'State Name',
            align: 'left',
            sortable: false,
            value: 'stateName'
          },
          { text: 'Abbr', value: 'Abbr' },
          { text: 'Contact Person', value: 'contactPerson' },
          { text: 'Email', value: 'Email' },
          { text: 'Mobile', value: 'Mobile' },
          { text: 'Actions', value: 'Actions' }
        ]
      }
    },
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Create - State' : 'Edit State'
      }
    },

    watch: {
      dialog (val) {
        val || this.close()
      }
    },
    async created(){
      try{
        this.desserts = await PostState.getPosts();
      }
      catch(err){
        this.error = err.message;
      }
    },    
    methods: {
      async deleteItem (item) {
        const index = this.desserts.indexOf(item)
        var r = confirm('Are you sure you want to delete this item?')
        if(r == true){
          this.desserts.splice(index, 1)
          await PostState.deletePost(item._id);
          this.desserts = await PostState.getPosts();
        }else{
          return false
        }
      },
      async editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        console.log(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      close() {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },
      async save() {
         if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else if (this.editedItem == '') {
          alert('done')
        }else {
            await PostState.insertPost(this.editedItem)
            .then(data=>{
              if (data.status == '200') {
                this.color = 'success'
                this.snackbar = true;
                this.text = 'State has been save successfully'
                this.close()
              }else if (data == 'Error: Request failed with status code 400') {
                this.color = 'error'
                this.snackbar = true;
                this.text = 'Error in saving data hint: All fields are required and state name should be unique'
              }else{
                this.color = 'error'
                this.snackbar = true;
                this.text = 'This state already exist'
              }
            }) 
        }
        this.desserts = await PostState.getPosts();
      },
    },
  }
</script>