import axios from 'axios';

const url = 'api/area';
const secondUrl = 'api/area/';

class PostState {
    // Get Posts
    static getPosts() {
        return new Promise(async (resolve, reject) => {
           try {
               const res = await axios.get(url)
               const data = res.data;
               resolve(
                   data.map(post => ({
                       ...post,
                       createdAt: new Date(post.createdAt)
                   }))  
               );
           }catch(err){
               return reject(err);
           }
        })
    }

    static getAreaByCenter(state) {
        return new Promise(async (resolve, reject) => {
           try {
               const res = await axios.get(`${secondUrl}${state}`)
               const data = res.data.data;
               resolve(
                   data.map(post => ({
                       ...post,
                       Date: new Date(post.Date)
                   }))  
               );
           }catch(err){
               return reject(err);
           }
        })
    }
    

    static insertPost(data){
      return axios.post(url, {
        areaName : data.areaName,
        stateName: data.stateName,
        contactPerson: data.contactPerson,
        Email: data.Email,
        Mobile: data.Mobile
      })
      .then(function(res){
          return(res)
      }).catch((err) =>{
          return(err)
      })
    }

    static deletePost(id){
      return axios.delete(`${secondUrl}${id}`);
    }
}

export default PostState;