<template>
  <div>
    <v-card>
    <v-snackbar 
      v-model="snackbar"
      :bottom="y = 'bottom'"
      :multi-line="mode = 'multi-line'"
      :right="x = 'right'"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}
      <v-btn
        color="black"
        flat
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </v-card>
    <v-toolbar flat color="white">
      <v-toolbar-title>BATCH</v-toolbar-title>
      <v-card-title>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <h2 class="error" v-if="error">{{ error }}</h2>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <v-btn slot="activator" color="primary" dark class="mb-2" @click="getStates">Create - Batch</v-btn>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.batchName" label="Batch Name"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.batchID" label="Batch ID"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                    <v-select 
                    :items="stateItem" 
                    v-on:change="changeArea"
                    label="State Name"
                    v-model="editedItem.stateName"
                    id="stateName"
                    ></v-select>
                </v-flex>
                <v-flex xs12 sm6 md6>
                    <v-select 
                    :items="areaItem" 
                    v-on:change="changeCenter"
                    label="Area Name"
                    v-model="editedItem.areaName"
                    ></v-select>
                </v-flex>
                <v-flex xs12 sm6 md6>
                    <v-select 
                    :items="centerItem" 
                    label="Center Name"
                    v-model="editedItem.centerName"
                    ></v-select>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-dialog
                    ref="dialog1"
                    v-model="modal"
                    :return-value.sync="date"
                    persistent
                    lazy
                    full-width
                    width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="date"
                      label="Start Date"
                      prepend-icon="event"
                      readonly
                    ></v-text-field>
                    <v-date-picker v-model="editedItem.startDate" scrollable>
                      <v-spacer></v-spacer>
                      <v-btn flat color="primary" @click="modal = false">Cancel</v-btn>
                      <v-btn flat color="primary" @click="$refs.dialog1.save(editedItem.startDate)">OK</v-btn>
                    </v-date-picker>
                  </v-dialog>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.actualClassLimit" label="Actual Class Of Limit"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                    <v-select 
                    :items="Weeks"
                    label="Batch Day"
                    v-model="editedItem.batchDay"
                    ></v-select>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-dialog
                    ref="dialog"
                    v-model="modal2"
                    :return-value.sync="time"
                    persistent
                    lazy
                    full-width
                    width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="time"
                      label="Batch Schedule"
                      prepend-icon="access_time"
                      readonly
                    ></v-text-field>
                    <v-time-picker
                      v-if="modal2"
                      v-model="editedItem.batchSchedule"
                      full-width
                    >
                      <v-spacer></v-spacer>
                      <v-btn flat color="primary" @click="modal2 = false">Cancel</v-btn>
                      <v-btn flat color="primary" @click="$refs.dialog.save(editedItem.batchSchedule)">OK</v-btn>
                    </v-time-picker>
                  </v-dialog>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field v-model="editedItem.teacher" type="tel" label="Teacher"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
            <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <tr>
          <td>{{ props.item.batchName }}</td>
          <td>{{ props.item.batchID }}</td>
          <td>{{ props.item.centerName }}</td>
          <td>{{ props.item.batchDay }}</td>
          <td>{{ props.item.batchSchedule }}</td>
          <td>{{ props.item.teacher }}</td>
          <td>
            <v-icon
              small
              class="mr-2"
              @click="editItem(props.item)"
            >
              edit
            </v-icon>
            <v-icon
              small
              class="mr-2"
              @click="editItem(props.item)"
            >
              list
            </v-icon>
            <v-icon
              small
              @click="deleteItem(props.item)"
            >
              delete
            </v-icon>
          </td>
        </tr>
      </template>
      <v-alert slot="no-results" :value="true" color="error" icon="warning">
        Your search for "{{ search }}" found no results.
      </v-alert>
    </v-data-table>
  </div>
</template>

<script>

  import PostArea  from '../http/PostArea';
  import PostCenter  from '../http/PostCenter';
  import PostState  from '../http/PostState';
  import PostBatch  from '../http/PostBatch';
  import Vue from 'vue'
  import VueCookie from 'vue-cookie'
  Vue.use(VueCookie )

  export default {
    name: 'Home',
    data() {
      return {
        date: new Date().toISOString().substr(0, 10),
        modal: false,
        time: null,
        modal2: false,
        snackbar: false,
        color: '',
        y: 'right',
        x: null,
        mode: 'vertical',
        timeout: 4000,
        text: '',
        search: '',
        Weeks: ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'],
        desserts: [],
        error: '',
        dialog: false,
        editedIndex: -1,
        editedItem: {
          batchName: '',
          batchID: '',
          stateName: '',
          areaName: '',
          centerName: '',
          startDate: '',
          actualClassLimit: '',
          batchDay: '',
          batchSchedule: '',
          teacher: ''
        },
        defaultItem: {
          batchName: '',
          batchID: '',
          stateName: '',
          areaName: '',
          centerName: '',
          startDate: '',
          actualClassLimit: '',
          batchDay: '',
          batchSchedule: '',
          teacher: ''
        },
        headers: [
          {text: 'Name',align: 'left',sortable: false,value: 'centerName'},
          { text: 'ID', value: 'stateName' },
          { text: 'Center', value: 'areaName' },
          { text: 'Batch Day', value: 'contactPerson' },
          { text: 'Batch Schedule', value: 'Email' },
          { text: 'Teacher', value: 'Mobile' },
          { text: 'Actions', value: 'Actions' }
        ],
        stateItem: [],
        areaItem: [],
        centerItem: [],
      }
    },
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Create - Batch' : 'Edit - Batch'
      }
    },

    watch: {
      dialog (val) {
        val || this.close()
      }
    },
    async created(){
      try{
        this.desserts = await PostBatch.getPosts();
      }
      catch(err){
        this.error = err.message;
      }
    },    
    methods: {
        async getStates() {
            var data = await PostState.getPosts();
            var list = []
            data.map(function(value) {
                list.push(value.stateName);
            });
            this.stateItem = list
        },
        async changeArea(a) {
            const data = await PostArea.getAreaByCenter(a);
            var listArea = []
            data.map(function(value) {
                listArea.push(value.areaName);
            });
            this.areaItem = listArea
        },
        async changeCenter(b) {
          const a = this.editedItem.stateName
          const data = await PostCenter.getCenterByStateArea(a, b);
          var listArea = []
          data.map(function(value) {
            listArea.push(value.centerName);
            });
            this.centerItem = listArea
        },
        async deleteItem (item) {
            const index = this.desserts.indexOf(item)
            var r = confirm('Are you sure you want to delete this item?')
            if(r == true){
            this.desserts.splice(index, 1)
            await PostBatch.deletePost(item._id);
            this.desserts = await PostBatch.getPosts();
            }else{
            return false
            }
        },
        async editItem (item) {
            this.editedIndex = this.desserts.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },
        close() {
            this.dialog = false
            setTimeout(() => {
            this.editedItem = Object.assign({}, this.defaultItem)
            this.editedIndex = -1
            }, 300)
        },
      async save() {
         if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else if (this.editedItem == '') {
          alert('done')
        }else {
            await PostBatch.insertPost(this.editedItem)
            .then(data=>{
                if (data == 'Error: Request failed with status code 400') {
                    this.color = 'error'
                    this.snackbar = true;
                    this.text = 'Error in saving data hint: All fields are required and state name should be unique'
                }else if (data.data.status == 'success') {
                    this.color = 'success'
                    this.snackbar = true;
                    this.text = data.data.message
                    this.close()
                }else{
                    this.color = 'error'
                    this.snackbar = true;
                    this.text = 'This Batch already exist'
                    }
            }) 
        }
        this.desserts = await PostBatch.getPosts();
      },
    },
  }
</script>