<script>
  import Login from './Login.vue'
  import Vue from 'vue'
  import VueJWT from 'vuejs-jwt'
  
  Vue.use(VueJWT)
  export default {
    components: {
      Login
    },
    data() {
      return {
        drawer: false,
        verifyUser: false,
        SupperAdmin: false,
        Teacher: false,
        username: '',
        
        // SUPER ADMIN AREA
        actions: [
          {icon: 'folder', text: 'State Management', route: '/state-management'},
          {icon: 'folder',text: 'Area Management',route: '/area-management'},
          {icon: 'folder',text: 'Center Management',route: '/center-management'},
          {icon: 'folder',text: 'Batch Management',route: '/batch-management'},
          {icon: 'folder',text: 'Student Management',route: ''}
        ],
        reports: [{icon: 'folder',text: 'Student Reports',route: '/state-management'},
          {icon: 'folder', text: 'Student Records', route: '/projects'},
          {icon: 'folder', text: 'Back Date Entry', route: '/teams'},
          {icon: 'folder', text: 'Absent Records', route: '/teams'},
          {icon: 'folder', text: 'Day End Report', route: '/teams'}],
        items: [
          {icon: 'dashboard',text: 'Moderators',route: '/moderators'},
          {icon: 'settings',text: 'settings',route: '/settings'},
        ],

        // TEACHER AREA
        TeacherProfile: [
          {icon: 'settings',text: 'settings',route: '/settings'},
        ],
        TeacherAction: [
          {icon: 'folder',text: 'Batch Management',route: '/teams'},
          {icon: 'folder',text: 'Student Management',route: '/teams'},
          {icon: 'folder',text: 'Add New Lesson',route: '/teams'}
        ],
        mini: false,
        right: null,
      }
    },
    mounted () {
      const verifyToken = localStorage.getItem('token')
      if (verifyToken == 'undefined') {
        this.$router.push('/ucmase-login')
      } else if (verifyToken == null) {
        this.$router.push('/ucmase-login')
      }else{
        const verifyUserData = this.$jwt.decode(verifyToken, 'vZiIpmTzqXHp8PpYXTwqc9SRQ1UAyAfC')
        this.username = verifyUserData.firstName + ' ' + verifyUserData.lastName 
        if (verifyUserData.moderator == 'Super Admin') {
          this.SupperAdmin = true
        }else if (verifyUserData.moderator == 'Teacher') {
          this.Teacher = true
        }
        this.verifyUser = true
        this.drawer = true
      }
    },
  }
</script>